import {
	_formatDate
} from '@/utils/common.js'

export default {
	props: {
		hotCommentsData: {
			type: Array,
			default () {
				return []
			}
		}
	},
	filters: {
		//时间戳转换
		showDate(value) {
			//1.将时间戳转换成Date对象
			const date = new Date(value)
			//2.将Date对象进行格式化
			return _formatDate(date, 'yyyy-MM-dd hh:mm:ss')
		}
	}
}
